import React from 'react'

//design assets
import smile from '../imgs/smile.PNG'
import insta from '../imgs/instagram.png'
import twitter from '../imgs/twitter.png'
import snap from '../imgs/snapchat.png'

function Footer(){
    return(
        <footer>
            <div className="footer-box">
                <div className="footbox">
                    <div className="footer-panda">
                        <img src={smile} alt='Kaja Panda'/>
                    </div>
                    <div className="footer-icons">
                        <img src={twitter} alt="" srcset="" className="footer-icon"/>
                        <img src={snap} alt="" srcset="" className="footer-icon"/>
                        <img src={insta} alt="" srcset="" className="footer-icon"/>
                    </div>
                </div>
                

                <div className="inquiry">
                    <h2>Inquiry?</h2>
                    <p>
                        Work with me....Lorem ipsum dolor sit amet, consectetur 
                        adipiscing elit. Vestibulum at arcu nec metus viverra suscipit. 
                        Pellentesque habitant morbi tristique senectus et netus et 
                        malesuada fames ac turpis egestas.
                    </p>
                </div>
                <div className="copyright">
                    <p className='cr'>@2020 Celave, LLC All rights reserved.</p>
                </div>
            </div>


        </footer>
    );
}

export default Footer;

